  .ltr {
    text-align: left;
  }
  
  .rtl {
    text-align: right;
  }
  
  .editor-input {
    min-height: 150px;
    resize: none;
    font-size: 16px;
    color: #2D3748;
    position: relative;
    tab-size: 1;
    outline: 0;
    padding: 16px 16px;
    caret-color: #718096;
  }
  
  .editor-text-bold {
    font-weight: bold;
  }
  
  .editor-text-italic {
    font-style: italic;
  }
  
  .editor-text-underline {
    text-decoration: underline;
  }
  
  .editor-text-strikethrough {
    text-decoration: line-through;
  }
  
  .editor-text-underlineStrikethrough {
    text-decoration: underline line-through;
  }
    
  .editor-tokenComment {
    color: slategray;
  }
  
  .editor-tokenPunctuation {
    color: #999;
  }
  
  .editor-tokenProperty {
    color: #905;
  }
  
  .editor-tokenSelector {
    color: #690;
  }
  
  .editor-tokenOperator {
    color: #9a6e3a;
  }
  
  .editor-tokenAttr {
    color: #07a;
  }
  
  .editor-tokenVariable {
    color: #e90;
  }
  
  .editor-tokenFunction {
    color: #dd4a68;
  }
  
  .editor-paragraph {
    margin: 0;
    margin-bottom: 8px;
    position: relative;
  }
  
  .editor-paragraph:last-child {
    margin-bottom: 0;
  }
  
  .editor-heading-h1 {
    font-size: 18px;
    font-weight: 700;
    margin: 0;
    margin-bottom: 12px;
    padding: 0;
  }
  
  .editor-heading-h2 {
    font-size: 16px;
    color: #718096;
    font-weight: 700;
    margin: 0;
    margin-top: 10px;
    padding: 0;
    text-transform: uppercase;
  }
  
  .editor-list-ol {
    padding: 0;
    margin: 0;
    margin-left: 4px;
  }
  
  .editor-list-ul {
    padding: 0;
    margin: 0;
    margin-left: 4px;
  }
  
  .editor-listitem {
    margin: 8px 16px 8px 16px;
  }
  
  .editor-nested-listitem {
    list-style-type: none;
  }
  
  pre::-webkit-scrollbar {
    background: transparent;
    width: 10px;
  }
  
  pre::-webkit-scrollbar-thumb {
    background: #999;
  }
  
  .clipboard {
    display: flex;
    flex-grow:1;
    justify-content: flex-end;
    padding-right: 6px;
  }

  .toolbar {
    display: flex;
    margin-bottom: 1px;
    background: #fff;
    padding: 4px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    vertical-align: middle;
    flex-wrap: wrap
  }
  
  .toolbar button.toolbar-item {
    border: 0;
    display: flex;
    background: none;
    border-radius: 10px;
    padding: 8px;
    cursor: pointer;
    vertical-align: middle;
  }
  
  .toolbar button.toolbar-item:disabled {
    cursor: not-allowed;
  }
  
  .toolbar button.toolbar-item.spaced {
    margin-right: 2px;
  }
  
  .toolbar button.toolbar-item i.format {
    background-size: contain;
    display: inline-block;
    height: 18px;
    width: 18px;
    margin-top: 2px;
    vertical-align: -0.25em;
    display: flex;
    opacity: 0.6;
  }
  
  .toolbar button.toolbar-item:disabled i.format {
    opacity: 0.2;
  }
  
  .toolbar button.toolbar-item.active {
    background-color: #EDF2F7;
  }
  
  .toolbar button.toolbar-item.active i {
    opacity: 1;
  }
  
  .toolbar .toolbar-item:hover:not([disabled]) {
    background-color: #CBD5E0;
  }
  
  .toolbar .divider {
    width: 1px;
    background-color: #EDF2F7;
    margin: 0 4px;
  }
  
  .toolbar select.toolbar-item {
    border: 0;
    display: flex;
    background: none;
    border-radius: 10px;
    padding: 8px;
    vertical-align: middle;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 70px;
    font-size: 14px;
    color: #2D3748;
    text-overflow: ellipsis;
  }
  
  .toolbar .toolbar-item .text {
    display: flex;
    line-height: 20px;
    width: 200px;
    vertical-align: middle;
    font-size: 14px;
    color: #2D3748;
    text-overflow: ellipsis;
    width: 70px;
    overflow: hidden;
    height: 20px;
    text-align: left;
  }
  
  .toolbar .toolbar-item .icon {
    display: flex;
    width: 20px;
    height: 20px;
    user-select: none;
    margin-right: 8px;
    line-height: 16px;
    background-size: contain;
  }
  
  .toolbar i.chevron-down {
    margin-top: 3px;
    width: 16px;
    height: 16px;
    display: flex;
    user-select: none;
  }
  
  .toolbar i.chevron-down.inside {
    width: 16px;
    height: 16px;
    display: flex;
    margin-left: -25px;
    margin-top: 11px;
    margin-right: 10px;
    pointer-events: none;
  }
  
  i.chevron-down {
    background-color: transparent;
    background-size: contain;
    display: inline-block;
    height: 8px;
    width: 8px;
    background-image: url(images/icons/chevron-down.svg);
  }
  
  #block-controls button:hover {
    background-color: #E2E8F0;
  }
  
  #block-controls button:focus-visible {
    border-color: #3182CE;
  }
  
  #block-controls span.block-type {
    background-size: contain;
    display: block;
    width: 18px;
    height: 18px;
    margin: 2px;
  }
  
  #block-controls span.block-type.paragraph {
    background-image: url(images/icons/text-paragraph.svg);
  }
  
  #block-controls span.block-type.h1 {
    background-image: url(images/icons/type-h1.svg);
  }
  
  #block-controls span.block-type.h2 {
    background-image: url(images/icons/type-h2.svg);
  }
    
  #block-controls span.block-type.ul {
    background-image: url(images/icons/list-ul.svg);
  }
  
  #block-controls span.block-type.ol {
    background-image: url(images/icons/list-ol.svg);
  }
  
  .dropdown {
    z-index: 5;
    display: block;
    position: absolute;
    box-shadow: 0 12px 28px 0 rgba(0, 0, 0, 0.2), 0 2px 4px 0 rgba(0, 0, 0, 0.1),
      inset 0 0 0 1px rgba(255, 255, 255, 0.5);
    border-radius: 8px;
    min-width: 100px;
    min-height: 40px;
    background-color: #fff;
  }
  
  .dropdown .item {
    margin: 0 8px 0 8px;
    padding: 8px;
    color: #2D3748;
    cursor: pointer;
    line-height: 16px;
    font-size: 15px;
    display: flex;
    align-content: center;
    flex-direction: row;
    flex-shrink: 0;
    justify-content: space-between;
    background-color: #fff;
    border-radius: 8px;
    border: 0;
    min-width: 268px;
  }
  
  .dropdown .item .active {
    display: flex;
    width: 20px;
    height: 20px;
    background-size: contain;
  }
  
  .dropdown .item:first-child {
    margin-top: 8px;
  }
  
  .dropdown .item:last-child {
    margin-bottom: 8px;
  }
  
  .dropdown .item:hover {
    background-color: #E2E8F0;
  }
  
  .dropdown .item .text {
    display: flex;
    line-height: 20px;
    flex-grow: 1;
    width: 200px;
  }
  
  .dropdown .item .icon {
    display: flex;
    width: 20px;
    height: 20px;
    user-select: none;
    margin-right: 12px;
    line-height: 16px;
    background-size: contain;
  }
  

  i.undo {
    background-image: url(images/icons/arrow-counterclockwise.svg);
  }
  
  i.redo {
    background-image: url(images/icons/arrow-clockwise.svg);
  }
  
  .icon.paragraph {
    background-image: url(images/icons/text-paragraph.svg);
  }
  
  .icon.large-heading,
  .icon.h1 {
    background-image: url(images/icons/type-h1.svg);
  }
  
  .icon.small-heading,
  .icon.h2 {
    background-image: url(images/icons/type-h2.svg);
  }
  
  .icon.bullet-list,
  .icon.ul {
    background-image: url(images/icons/list-ul.svg);
  }
  
  .icon.numbered-list,
  .icon.ol {
    background-image: url(images/icons/list-ol.svg);
  }
   
  i.bold {
    background-image: url(images/icons/type-bold.svg);
  }
  
  i.italic {
    background-image: url(images/icons/type-italic.svg);
  }
  
  i.underline {
    background-image: url(images/icons/type-underline.svg);
  }
  
  i.strikethrough {
    background-image: url(images/icons/type-strikethrough.svg);
  }
    
  i.left-align {
    background-image: url(images/icons/text-left.svg);
  }
  
  i.center-align {
    background-image: url(images/icons/text-center.svg);
  }
  
  i.right-align {
    background-image: url(images/icons/text-right.svg);
  }
  
  i.justify-align {
    background-image: url(images/icons/justify.svg);
  }
  